<template>
  <div>
    <Width100 v-bind:addPadding="true">
      <Width50>
        <Paragraph
          subtitle="The Problem"
          paragraph="Getting a tattoo is an incredibly personal experience and the overall process of exploring designs and finding an artist could take months. Not many web or mobile apps address the complexity and emotional element of finding the right design and tattoo artist."
        />
        <Paragraph
          subtitle="The Solution"
          paragraph="The app should contain a platform that has high visual content where users can explore tattoo designs by tattoo artists from locally and globally. Users can direct message their tattoo artist to build a closer connection with their tattoo artist. The design structure should be intuitive where users can explore easily."
        />
        <Paragraph
          subtitle="Tools"
          paragraph="Invision (Prototype), Sketch & Balsamiq (Wireframes and Visual Design), UsabilityHub (Testing)"
        />
      </Width50>
      <Width50>
        <div class="carousel-container">
          <Carousel
            v-bind:items="items"
            :containerStyle="containerStyle"
            :imgStyle="imgStyle"
          />
        </div>
      </Width50>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import LineBreak from '../common/LineBreak';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import prototype_screen1 from "../../assets/img/prototype_screen1.png";
import prototype_screen2 from "../../assets/img/prototype_screen2.png";
import prototype_screen3 from "../../assets/img/prototype_screen3.png";
import prototype_screen4 from "../../assets/img/prototype_screen4.png";
import prototype_screen5 from "../../assets/img/prototype_screen5.png";
import prototype_screen6 from "../../assets/img/prototype_screen6.png";

export default {
  components: {
    Carousel,
    LineBreak,
    Paragraph,
    Width50,
    Width100,
  },
  data() {
    return {
      items: [
        {img: prototype_screen1, alt: "Prototype"},
        {img: prototype_screen2, alt: "Prototype"},
        {img: prototype_screen3, alt: "Prototype"},
        {img: prototype_screen4, alt: "Prototype"},
        {img: prototype_screen5, alt: "Prototype"},
        {img: prototype_screen6, alt: "Prototype"},
      ],
      containerStyle: {
        height: "600px"
      },
      imgStyle: {
        padding: "24px",
        height: "100%"
      },
    }
  },
};
</script>

<style lang="scss" scoped>
.carousel-container {
  margin: 0 auto;
}
</style>



